'use strict'

import './index.scss'
import 'material-design-icons'

// third parties
import angular from 'angular'
import ngResource from 'angular-resource'
import uiRouter from 'angular-ui-router'

// resources
import {
    User
} from './services/resources'

// services
import initConfig from './config'
import Security from './services/security'

// // components

// layout
import navBar from './components/layout/navBar/navBar'

// stateViews
import home from './components/stateViews/home/home'
import feedback from './components/stateViews/feedback/feedback'
import login from './components/stateViews/login/login'
import signup from './components/stateViews/signup/signup'
import offline from './components/stateViews/offline/offline'

// utils
import bubble from './components/utils/bubble/bubble'

const deps = [
    ngResource,
    uiRouter
]

const run = [
    '$state',
    '$transitions',
    'Security',
    '$http',
    Run
]

angular.module('bridgeData', deps)

    .factory('User', User)
    .factory('Security', Security)

    .config(initConfig)
    .run(run)

    .component('navBar', navBar)
    .component('home', home)
    .component('feedback', feedback)
    .component('login', login)
    .component('signup', signup)
    .component('offline', offline)
    .component('bubble', bubble)

// -------------------------------------------------------

function Run($state, $transitions, Security, $http) {
    $http.defaults.headers.post['Content-Type'] = 'application/json'
    if (!navigator.onLine) {
        $state.go('offline')
    }
    $transitions.onSuccess({}, () => {
        Security.refetch().then((isAuth) => {
            if (!isAuth) {
                if ($state.current.name !== "signup" && $state.current.name !== "login") {
                    Security.logout()
                }
            } else {
                $state.go('home')
            }
        })
    })
}



