'use strict'

const states = [
    {
        name: 'home',
        url: '/',
        component: 'home'
    },
    {
        name: 'feedback',
        url: '/feedback',
        component: 'feedback'
    },
    {
        name: 'login',
        url: '/login',
        component: 'login'
    },
    {
        name: 'signup',
        url: '/signup',
        component: 'signup'
    },
    {
        name: 'offline',
        url: '/offline',
        component: 'offline'
    }

]

function init($urlRouterProvider, $stateProvider) {
    states.forEach((state) => {
        $stateProvider.state(state)
    })

    $urlRouterProvider.otherwise('/')

    
}

const config = [
    '$urlRouterProvider',
    '$stateProvider',
    init
]

export default config


