'use strict'

class Offline {
    constructor($state, $interval) {
        $interval(() => {
            if (navigator.onLine) {
                $state.go('home')
            }
        }, 1000)
    }
}

const offlineController = [
    '$state',
    '$interval',
    Offline
]

const offline = {
    template: require('./offline.html'),
    controller: offlineController
}

export default offline


