'use strict'

class NavBar {
    constructor(Security) {
        this.Security = Security
    }

    logout() {
        this.Security.logout()
    }
}

const navBarController = [
    'Security',
    NavBar
]

const navBar = {
    template: require('./navBar.html'),
    controller: navBarController
}

export default navBar


