'use strict'

require('./home.scss')

class Home {
    constructor(User) {
        this.User = User
        this.fetchUsers()
    }

    async fetchUsers() {
        this.users = await this.User.query()
    }

    getRelease(id) {
        return this.Release.get({id})
    }

    getArtist(id) {
        return this.Artist.get({id})
    }
}

const homeController = [
    'User',
    Home
]

const home = {
    template: require('./home.html'),
    controller: homeController
}

export default home


