'use strict'

const url = 'http://localhost:9002'

class Security {
    constructor($state, $location, $http, $q) {
        this.state = $state
        this.location = $location
        this.http = $http
        this.q = $q
        this.authenticate = false
    }

    refetch() {
        return this.q((resolve, reject) => {
            this.http.get('http://localhost:9002/auth')
                .then((data) => {
                    this.authenticate = data.isAuth
                    resolve(this.authenticate)
                })
        })
    }

    login(user) {
        return this.q((resolve, reject) => {
            this.http.post(url + '/login', user)
                .then((response) => {
                    this.authenticate = true
                    resolve(response)
                })
                .catch((err) => {
                    this.authenticate = false
                    resolve(err)
                })
        })
    }

    signup(user) {
        return this.q((resolve, reject) => {
            this.http.post(url + '/signup', user).$promise
                .then((response) => {
                    this.authenticate = true
                    resolve(response)
                })
                .catch((err) => {
                    this.authenticate = false
                    reject(err)
                })
        })
    }

    isAuth() {
        return this.authenticate
    }

    logout() {
        this.http.get(url + '/logout').then((response) => {
            this.authenticate = false
            this.state.go('login')
        })
    }
}

const security = [
    '$state',
    '$location',
    '$http',
    '$q',
    Security
]

export default security


