'use strict'

class Signup {
    constructor(Security) {
        this.user = {
            username: "",
            password: "",
            email: ""
        }
        this.Security = Security
    }

    signup() {
        this.Security.signup(this.user)
            .then(() => {

            })
    }
}

const signupController = [
    "Security",
    Signup
]

const signup = {
    template: require('./signup.html'),
    controller: signupController
}

export default signup


