'use strict'

class Login {
    constructor(Security, $state) {
        this.user = {
            username: "",
            password: ""
        }
        this.Security = Security
        this.state = $state
    }

    async login() {
        const response = await this.Security.login(this.user)

        if (response) {
            this.state.go('home')
        }
    }
}

const loginController = [
    "Security",
    "$state",
    Login
]

const login = {
    template: require('./login.html'),
    controller: loginController
}

export default login


